:root {
  --suisse: suisse;
  --gilroy: gilroy;
  --white: #ffffff;
  --black: #464646;
  --grey: #DEDEDE;
  --brown: #72584F;
  --light-brown: #F5F2EF;
  --gold: #D8B86F;
  --gold-dark: #9F823C;
  --gold-gradient: linear-gradient(94.18deg, #9F823C 0%, #D8B86F 62.68%);
  --green: #40664C;
  --green-gradient: linear-gradient(267.47deg, #40664C 0.98%, #709471 75.13%);
  --radius: 15px;
  --transition: 0.3s ease all;
  --primary-color: #131313;
}

@keyframes rotateText {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.choose {
  display: flex !important;
  align-items: center;
  justify-content: center;
  gap: 12px;
  background-color: rgba(0, 0, 0, 0.15);
  height: 50px;
  border: 1px solid #fff;
  font-weight: 400;
  transition: 0.3s ease-in-out all;
  max-width: 236px;
  font-size: 13px;
  position: relative;
}
.choose svg path {
  transition: 0.6s ease all;
}
.choose svg {
  transition: 0.6s ease all;
}
.choose:hover {
  background-color: #fff;
  color: var(--gold);
  border-color: var(--gold);
}
.choose:hover svg path {
  fill: var(--gold);
}
.choose:hover svg {
  transform: rotate(90deg);
}
.choose.golden {
  background: var(--gold-gradient);
  color: var(--white);
  border: none;
  position: relative;
  z-index: 1;
}
.choose.golden::before {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-color: var(--white);
  z-index: 0;
  opacity: 0;
  transition: 0.3s ease all;
}

.choose.golden:hover {
  border: 1px solid var(--gold);
  color: var(--gold);
}
.choose.golden:hover span {
  z-index: 1;
}
.choose.golden:hover::before {
  opacity: 1;
}
.choose.golden:hover svg path {
  fill: var(--gold);
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  opacity: 0;
  visibility: hidden;
  transition: 0.3s ease all;
  z-index: 3;
}
.overlay.active {
  visibility: visible;
  opacity: 1;
}

.bg-title,
.bg-title-bottom {
  position: absolute;
  transform: translate(-80px, 80px);
  opacity: 0;
  will-change: transform, opacity;
  white-space: nowrap;
  font-size: 128px;
  line-height: 1;
  font-weight: 500;
  text-transform: uppercase;
  color: rgba(114, 88, 79, 0.1607843137);
  transition: opacity 0.6s ease-out;
}

.bg-title {
  top: 0;
  right: -4%;
}

.bg-title-bottom {
  bottom: -74px;
  left: 0;
  transform: translate(-80px, 80px);
  opacity: 0;
  will-change: transform, opacity;
  opacity: 1;
}

.bg-title.is-visible {
  transform: translate(-50%, 80px);
  opacity: 1;
}

.title {
  font-size: 48px;
  font-weight: 500;
  line-height: 1.2;
}

.text {
  font-size: 18px;
  line-height: 1.4;
  font-weight: 300;
}

.text-gold {
  font-size: 32px;
  font-weight: 500;
  line-height: 1.2;
  color: var(--gold);
}

.button {
  background: var(--gold-gradient);
  display: flex;
  align-items: center;
  gap: 2px;
  font-size: 13px;
  font-weight: 450;
  color: var(--white);
  display: inline-flex;
  padding: 0px 16px;
  height: 50px;
  text-transform: uppercase;
  border: 1px solid var(--gold);
  position: relative;
  transition: 0.3s ease all;
}
.button span {
  z-index: 1;
}
.button svg {
  z-index: 1;
  transition: 0.3s ease all;
}
.button svg path {
  transition: 0.3s ease all;
}
.button:hover {
  color: var(--gold);
  border: 1px solid var(--gold);
}
.button:hover svg {
  transform: rotate(45deg);
}
.button:hover svg path {
  stroke: var(--gold);
}
.button:hover::before {
  opacity: 1;
  visibility: visible;
}
.button:active {
  color: var(--white);
}
.button:active::before {
  opacity: 1;
  visibility: visible;
  background-color: var(--gold-dark);
}
.button:active svg path {
  stroke: var(--white);
}
.button::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #fff;
  opacity: 0;
  visibility: hidden;
  transition: 0.3s ease all;
}

.subtitle {
  display: flex;
  align-items: center;
  gap: 6px;
}
.subtitle span {
  color: var(--gold);
}

.cta__box {
  width: 133px;
  height: 133px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.cta__box .main__buttons_cta-arrow {
  z-index: 1;
  transition: 0.3s ease all;
}
.cta__box:hover .main__buttons_cta-arrow {
  transform: rotate(45deg);
}

.cta {
  background: var(--gold-gradient);
  border-radius: 50%;
  position: absolute;
  padding: 4px;
  width: 133px;
  height: 133px;
  font-weight: 300;
  fill: white;
  font-size: 17px;
  letter-spacing: 4px;
  text-transform: uppercase;
  pointer-events: none;
  animation: rotateText 16s linear infinite;
}
.cta text {
  font-family: inherit;
}

input {
  height: 50px;
  font-size: 16px;
  display: flex;
  align-items: center;
  border-bottom: 1px solid rgba(92, 92, 92, 0.3);
  font-weight: 450;
}
input::placeholder {
  font-weight: 300;
}

.mobile-only {
  display: none !important;
}

.desktop-only {
  display: block;
}

@media (max-width: 767px) {
  .text-gold {
    font-size: 24px;
    font-weight: 500;
    line-height: 1.2;
    color: var(--gold);
  }
  .bg-title {
    position: absolute;
    top: -44px;
    right: -4%;
    transform: translate(-80px, 80px);
    opacity: 0;
    will-change: transform, opacity;
    font-size: 64px;
    line-height: 1;
    font-weight: 500;
    text-transform: uppercase;
    color: rgba(114, 88, 79, 0.1607843137);
    transition: opacity 0.6s ease-out;
  }
  .bg-title-bottom {
    font-size: 64px;
    bottom: -13px;
    left: -4%;
  }
  .mobile-only {
    display: block !important;
  }
  .desktop-only {
    display: none !important;
  }
  .title {
    font-size: 30px;
    font-weight: 500;
    line-height: 1.2;
  }
  .text {
    font-size: 16px;
    font-weight: 300;
  }
}
.main {
  background-image: url(../img/jpg/bg.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  margin: 10px 20px 0px 20px;
  color: var(--white);
  display: flex;
  align-items: center;
  height: 910px;
}
.main__container {
  flex: 1 1 auto;
}
.main__content {
  display: flex;
  justify-content: space-between;
  padding: 155px 0px 0px 0px;
}
.main__titlebox {
  display: flex;
  flex-direction: column;
  gap: 42px;
}
.main__titlebox h1 {
  font-size: 72px;
  line-height: 1.2;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  font-weight: 400;
  max-width: 1004px;
}
.main__titlebox p {
  font-size: 28px;
  font-weight: 300;
  line-height: 1.4;
  color: var(--white);
}
.main__buttons {
  display: flex;
  flex-direction: column;
  gap: 36px;
}
.main__buttons_view {
  position: relative;
  border-radius: 50%;
  display: flex;
  flex-direction: column;
  text-align: center;
  position: relative;
  width: 133px;
  height: 133px;
  background: rgba(255, 255, 255, 0.15);
  backdrop-filter: blur(2.5px);
}
.main__buttons_view:hover span {
  opacity: 1;
  visibility: visible;
  letter-spacing: 1.5px;
}
.main__buttons_view:hover img {
  top: 20px;
}
.main__buttons_view::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background-image: url(../img/png/border.png);
  background-size: cover;
  background-position: center;
}
.main__buttons_view span {
  opacity: 0;
  visibility: hidden;
  transition: 0.6s ease all;
  font-size: 11px;
  text-transform: uppercase;
  position: absolute;
  bottom: 23px;
  line-height: 140%;
  letter-spacing: 10%;
  display: flex;
  text-align: center;
  width: 100%;
  justify-content: center;
}
.main__buttons_view img {
  width: 56px;
  height: 56px;
  position: absolute;
  top: calc(50% - 28px);
  left: calc(50% - 28px);
  transition: 0.6s ease all;
}

@media (max-width: 767px) {
  .main {
    background-image: url(../img/jpg/bg.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 13%;
    margin: 16px 16px 0px 16px;
    color: var(--white);
    display: flex;
    align-items: center;
    height: inherit;
  }
  .main__container {
    flex: 1 1 auto;
  }
  .main__content {
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    padding: 173px 0px 0px 0px;
  }
  .main__titlebox {
    display: flex;
    flex-direction: column;
    gap: 32px;
  }
  .main__titlebox h1 {
    font-size: 36px;
    line-height: 1.2;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    font-weight: 400;
    max-width: 1004px;
  }
  .main__titlebox p {
    font-size: 28px;
    font-weight: 300;
    line-height: 1.4;
    color: var(--white);
  }
  .main__titlebox .choose {
    max-width: 236px;
    font-size: 13px;
    margin-top: 4px;
    display: flex !important;
  }
  .main__buttons {
    display: flex;
    flex-direction: row;
    gap: 12px;
    margin-top: 75px;
    margin-bottom: 24px;
  }
  .main__buttons_view {
    position: relative;
    border-radius: 50%;
    display: flex;
    flex-direction: column;
    text-align: center;
    position: relative;
    width: 133px;
    height: 133px;
  }
  .main__buttons_view::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 50%;
    background: rgba(203, 203, 203, 0.2);
    filter: blur(1.5px);
    -webkit-backdrop-filter: blur(10px);
  }
  .main__buttons_view span {
    opacity: 1;
    visibility: visible;
    transition: 0.6s ease all;
    letter-spacing: 1.5px;
  }
  .main__buttons_view img {
    width: 48px;
    height: 48px;
    position: absolute;
    top: 22px;
    left: calc(50% - 24px);
  }
}
.header {
  color: var(--white);
  padding: 30px 20px;
  position: fixed;
  width: 100%;
  top: -111px;
  left: 0;
  border-bottom: 1px solid rgba(255, 255, 255, 0.3);
  transition: 0.3s ease all;
  z-index: 3;
}
.header.border {
  border: none;
}
.header.active {
  top: 0px;
}
.header.bg {
  background: #ffffff;
  color: var(--black);
}
.header.bg .header__burger::after, .header.bg .header__burger::before {
  content: "";
  background-color: var(--black);
}
.header.bg .header__burger span::after, .header.bg .header__burger span::before {
  background-color: var(--black);
}
.header.bg .header__logo a svg path {
  transition: 0.3s ease all;
  fill: #464646;
}
.header.bg .header__switcher {
  color: rgba(70, 70, 70, 0.4);
}
.header.bg .header__switcher a {
  transition: 0.3s ease all;
}
.header.bg .header__switcher a:hover {
  color: var(--black);
}
.header.bg .header__switcher a:nth-child(1) {
  position: relative;
}
.header.bg .header__switcher a:nth-child(1)::before {
  content: "";
  top: 0;
  right: -18px;
  background-color: var(--black);
}
.header.bg .header__switcher a.active {
  color: var(--black);
}
.header.bg .header__choose {
  background: var(--white);
  color: var(--gold);
  border-color: var(--gold);
}
.header.bg .header__choose svg path {
  fill: var(--gold);
}
.header.bg .header__choose:hover {
  background: var(--gold-gradient);
  border: none;
  color: var(--white);
}
.header.bg .header__choose:hover svg path {
  fill: var(--white);
